.justified{
	max-width: 90vh;
	max-height: 130vh;

}
.contanier{
	margin : 10vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

@media only screen and (max-width:414px){
	.contanier{
		margin : 10vh;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 100vh;
	}
}
@media only screen and (max-width:375px){
	.contanier{
		margin : 10vh;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 150vh;
	}
}
@media only screen and (max-width:320px){
	.contanier{
		margin : 10vh;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 250vh;
	}
}

article{
	transition: all 1s;
}
article:hover{
	transform: scale(1.1);
}
img{
	max-width: 90%;
	max-height:100%; 

}