.project-section {
  background: -webkit-linear-gradient(top, #260042 18%, #4B0082 79%, #6F339B);
  background: linear-gradient(to bottom, #260042 18%, #4B0082 79%, #6F339B);
  -webkit-clip-path: polygon(0 2%, 100% 0, 100% calc(100% - 40px), 0 100%);
          clip-path: polygon(0 2%, 100% 0, 100% calc(100% - 40px), 0 100%);
}

.project-section h1 {
  margin-bottom: 5rem;
  font-family: Norwester;
  padding-top: 50px;
}


.project-section h4 {
  font-family: Montserrat;
  font-weight: 100;
}

.customer-project-header {
  margin-top: 50px;
}

.projects-container {
  position: relative;
}

.customer-project-div {
  padding-bottom: 70px;
}

.individual-project-container {
  margin-right: 0.75rem;
  margin-top: 4rem;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center top;
}

.individual-project-container header {
  -webkit-transform: translateY(-3rem);
          transform: translateY(-3rem);
  margin-bottom: 0;
}

.individual-project-container header p {
  color: #260042;
  font-family: Norwester;
}

.individual-project-container header p a {
  color: #260042;
}

.front-card {
  margin-bottom: 0;
  font-family: Montserrat;
  top: 20%;
}

.cardContainer {
  position: relative;
  height: 16.875rem;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-perspective: 150em;
          perspective: 150em;
  box-shadow: 1em 1em 2em rgba(0,0,0,.2);
}

.card__side {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  width: 100%;
  -webkit-transition: all .6s ease;
  transition: all .6s ease;
  position: absolute;
  left: 0;
}

.card__side--back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  padding-top: 0;
}

.cardFlipped .card__side--front {
   -webkit-transform: rotateY(-180deg);
           transform: rotateY(-180deg);
}

.cardFlipped .card__side--back {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

@media only screen and (max-width: 767px) {
  .line__label {
    -webkit-transform: rotateY(45deg);
            transform: rotateY(45deg);
  }
}

.skill-section {
  background: -webkit-linear-gradient(top, #0a3e1c 18%, #157d39 79%, #1fbc56);
  background: linear-gradient(to bottom, #0a3e1c 18%, #157d39 79%, #1fbc56)
}

.skill-section h1 {
  margin-bottom: 5rem;
  font-family: Norwester;
  padding-top: 50px;
}

.skill-section h4 {
  font-family: Montserrat;
  font-weight: 100;
}

.skill-graph {
  margin-top: 60px;
  padding-bottom: 60px;
}

.clipBackground {
  -webkit-clip-path: polygon(0 40px, 100% 0, 100% 95%, 0% 100%);
          clip-path: polygon(0 40px, 100% 0, 100% 95%, 0% 100%);
  -webkit-transform: translateY(-41px);
          transform: translateY(-41px)
}

.skills {
  max-width: 80%;
  height: 570px;
  margin: auto;
  position: relative;
  margin-bottom: 50px;
  font-family: Montserrat;
  font-weight: 300;
}

.content ul {
  list-style-type: none;
  z-index: 10;
}

.lines {
  height: 100%;
  position: relative;
}

.line {
  height: inherit;
  width: 2px;
  position: absolute;
  background: rgba(238, 238, 238, 0.6);
}

.l--0 {
  left: 0;
}

.l--25 {
  left: 25%;
}

.l--50 {
  left: 50%;
}

.l--75 {
  left: 75%;
}

.l--100 {
  left: calc(100% - 1px);
}

.line__label {
  display: block;
  width: 100px;
  text-align: center;
  position: absolute;
  bottom: -20px;
  right: -50px;
}

.charts {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: hsl(0, 0%, 86%);
}

.chart {
  margin: 30px 0 0;
}

.chart:first-child {
  margin: 0;
}

.chart__title {
  display: block;
  margin: 0 0 10px;
  font-weight: bold;
  opacity: 0;
  -webkit-animation: 1s anim-lightspeed-in ease forwards;
          animation: 1s anim-lightspeed-in ease forwards;
}

.chart--dev {
  -webkit-animation-delay: 3.3s;
          animation-delay: 3.3s;
}

.chart--prod {
  -webkit-animation-delay: 3.3s;
          animation-delay: 3.3s;
}

.chart--design {
  -webkit-animation-delay: 4.5s;
          animation-delay: 4.5s;
}

.chart--horiz {
  overflow: hidden;
}

.chart__bar {
  height: 30px;
  margin-bottom: 10px;
  background: -webkit-linear-gradient(right, #4cb8c4, #3cd3aa);
  background: linear-gradient(to left, #4cb8c4, #3cd3aa);
}

.chart__label {
  padding-left: 10px;
  line-height: 30px;
  color: white;
}

.list-item {
  -webkit-animation-name: eachTechItem;
          animation-name: eachTechItem;
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  opacity: 0;
}

.chart ul li.list-item {
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  border-top-left-radius: 0;
}

.item {
  opacity: 0;
  -webkit-animation-name: revealItem;
          animation-name: revealItem;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes revealItem {
  from {opacity:0;}
  to {opacity:1;}
}

@keyframes revealItem {
  from {opacity:0;}
  to {opacity:1;}
}

@-webkit-keyframes eachTechItem {
  from {width:0%;}
  to {opacity:1;}
}

@keyframes eachTechItem {
  from {width:0%;}
  to {opacity:1;}
}

